.about {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: fit-content;
	background-color: #333;
	color: #fff;
	z-index: 1;
	padding-top: 6vh;
}

.experience-container {
	display: flex;
	flex-direction: column;
}

.icon {
